import React from 'react'

function Song( { currentSong }) {
    return (
        <div>
            <div className="song-container">
                <img src={currentSong.cover}/>
                <h1>{currentSong.name}</h1>
                <h2>{currentSong.artist}</h2>
            </div>
        </div>
    )
}

export default Song
