import React, {useRef, useState} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faAngleLeft,
  faAngleRight,
} from "@fortawesome/free-solid-svg-icons";
function Player( {currentSong, isPlaying, setIsPlaying} ) {
   const audioRef = useRef(null)
   const clickHandler = () =>{
     if(isPlaying){
         audioRef.current.pause()

         setIsPlaying(!isPlaying)
     }else{
         audioRef.current.play()
         setIsPlaying(!isPlaying)
     }
   }
   const getTime = (time) =>{
       return(
       Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
       )
   }
   const [timeUp,setTimeUp] = useState({
       currentTime: null,
       duration: null,
   })
   const timeHandler = (e) =>{
      const current = e.target.currentTime;
      const duration = e.target.duration;
      setTimeUp({...timeUp, currentTime:current, duration})
   }

  return (
    <div className="player">
      <div className="time-control">
        <p>{getTime(timeUp.currentTime)}</p>
        <input type="range" />
        <p>{getTime(timeUp.duration)}</p>
      </div>
      <div className="play-control">
        <FontAwesomeIcon className="skip-back" size="2x" icon={faAngleLeft} />
        <FontAwesomeIcon onClick={clickHandler} className="play" size="2x" icon={faPlay} />
        <FontAwesomeIcon className="skip-forward"size="2x" icon={faAngleRight} />
      </div>
      <audio onLoadedMetadata={timeHandler} onTimeUpdate={timeHandler} ref={audioRef} src={currentSong.audio}></audio>
    </div>
    
  );
}

export default Player;
